@import url('https://fonts.googleapis.com/css?family=Labrada|Wallpoet|VT323|Ubuntu|Julee|Lora|Kalam|Merienda');

.sign-up-container{
    display: flex;
    flex-direction: column ;
    align-items: center;
    row-gap: .5cm;
}
.sign-up-input{
    width: 250px;
    height: 30px;
    font-size: 18px;
    border: none;
    background-color: #fafafa;
    border-bottom: 1px solid black;
    /* border-radius: 5px; */
}
.signButton{
    background-color: #28282b;
    color: white;
    border: none;
    width: 100px;
    height: 40px;
    font-size: 21px;
    border-radius: 10px;
    cursor: pointer;
}

@media screen and (max-width:350px) {
    .sign-up-input{
        width: 200px;
        height: 30px;
        font-size: 17px;
    }
}