.buttons-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    row-gap: 1cm;
}
.googleButton{
    width: 200px;
    height: 40px;
    border: none;
    font-size: 23px;
    color: white;
    background-color: blueviolet;
    font-weight: 600;
    border-radius: 10px;
    font-family: Labrada;
    cursor: pointer;
}

@media screen and (max-width:350px) {
    h2{
        font-size: 20px;
    }
}